import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";

import clamp from "../../utils/clamp";
import zeroPad from "../../utils/zeroPad";
import TimelineLayer from "./TimelineLayer";
import { offsetToFraction, frameToMilliseconds, millisecondsToFrame } from "../../utils/units";

export default function Timeline({ currentTime }) {
    const [state, setState] = useState({
        isSeeking: false,
        seekStartX: 0,
        seekStartPos: 0,

        frame: {
            current: 0,
            count: 6000
        },

        seekerOffset: 0,

        layers: [
            {
                name: "dog",
                url: "test-memes/dog.png",
                frame: {
                    start: 0,
                    end: 6000,
                    count: 6000
                }
            },
            {
                name: "doggo",
                url: "test-memes/doggo.jpg",
                frame: {
                    start: 500,
                    end: 4000,
                    count: 6000
                }
            }
        ]
    });

    const frameToDisplayTime = (frame) => {
        const time = frameToMilliseconds(frame.current);

        const minutes = Math.floor(time / 60 / 1000);
        const seconds = Math.floor((time - minutes * 60 * 1000) / 1000);
        const millis = Math.floor(time - minutes * 60 * 1000 - seconds * 1000);

        return `${zeroPad(minutes, 2)}:${zeroPad(seconds, 2)}:${zeroPad(millis, 3)}`;
    };

    const onSeekStart = (event) => {
        const { clientX } = event;

        setState((state) => ({
            ...state,
            seekStartX: clientX,
            seekStartPos: state.seekerOffset,
            isSeeking: true
        }));
    };

    const onSeekStop = () => {
        setState((state) => ({
            ...state,
            isSeeking: false
        }));
    };

    const onMouseMove = (event) => {
        const { clientX } = event;

        if (state.isSeeking) {
            const min = 0;
            const max = 720 - 80; // timelineWidth - seekerWidth

            const seekerOffset = clamp((state.seekStartPos + clientX - state.seekStartX), min, max);

            setState((state) => ({
                ...state,
                seekerOffset,

                frame: {
                    ...state.frame,
                    current: Math.floor(offsetToFraction(seekerOffset) * state.frame.count)
                }
            }));
        }
    };

    useEffect(() => {
        document.addEventListener("mouseup", onSeekStop.bind(this));

        return function cleanup() {
            document.removeEventListener("mouseup", onSeekStop);
        };
    }, []);

    useEffect(() => {
        setState((state) => {
            return {
                ...state,

                frame: {
                    ...state.frame,
                    current: millisecondsToFrame(currentTime)
                }
            };
        });
    }, [currentTime]);

    return (
        <div id="timeline" onMouseUp={onSeekStop.bind(this)}>
            <div className="seekbar" onMouseMove={onMouseMove.bind(this)}>
                <div
                    className="seeker"
                    onMouseDown={onSeekStart.bind(this)}
                    style={{ left: state.seekerOffset }}
                >{`${frameToDisplayTime(state.frame)}`}</div>
                <div className="track"></div>
            </div>

            <div className="layers">
                {state.layers.map((item, index) => {
                    return (
                        <TimelineLayer
                            key={item.name}
                            name={item.name}
                            url={item.url}
                            frame={item.frame}
                        />
                    );
                })}
            </div>

            <div
                className="position"
                style={{ left: state.seekerOffset }}
            ></div>
        </div>
    );
}

Timeline.propTypes = {
    currentTime: PropTypes.number.isRequired
};
