import React, { useState } from "react";
import { host } from "../../consts";
import { useHistory } from "react-router-dom";

export default function Register() {
    const history = useHistory();
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [registering, setRegistering] = useState(false);
    const [error, setError] = useState(null);

    const submit = async e => {
        var fields = {
            captcha: "Animasher-beta", // todo: Proper captcha implementation
            name,
            password,
            email
        };
        setRegistering(true);
        e.preventDefault();

        var resp = await fetch(host + "/user/register", {
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(fields)
        });
        var res = await resp.json();

        if (res.error || !res.token) {
            setRegistering(false);
            switch (res.error) {
                case "bad_captcha":
                    return setError("Couldn't verify captcha");
                case "bad_format":
                    return setError("Some fields are missing");
                case "name_exist":
                    return setError("This name is already registered");

                default:
                    return setError("Unknown error happened");
            }
        }

        localStorage.setItem("token", res.token);
        history.replace("/");
    };

    return (
        <div>
            <form onSubmit={submit}>
                {error ? <p className="error">{error}</p> : ""}
                <input type="text" placeholder="name" value={name} onChange={e => setName(e.target.value)} />
                <input type="text" placeholder="email" value={email} onChange={e => setEmail(e.target.value)} />
                <input type="password" placeholder="password" value={password} onChange={e => setPassword(e.target.value)} />
                <input type="submit" value="Register" disabled={registering}/>
            </form>
        </div>
    );
}
