import React from "react";
import { Link } from "react-router-dom";

export default function Rules() {
    return (
        <div className="footer-page-static">
            <h1>How to behave on Animasher</h1>

            <h3 className="pinkheader">Rules</h3>
            <p style={{ margin: 0 }}>
                Animasher is a site where people have fun making animations.
                To make sure everyone feel safe and happy, it is important that we all follow some simple rules.
                These are:
            </p>
            <ul className="list">
                <li>
                    Be nice to each other. Swearing, bullying, threatening, harrassing, and insulting each other is NOT ok.
                    This goes for both comments, private messages, and animations.
                    If someone does this to you, send a private message to <Link to="/member/admin">admin</Link>.
                </li>
                <li>
                    You should NEVER use someone else&apos;s account without their permission.
                    It doesn&apos;t matter if you asked for their password and they gave it to you.
                    Only use your own account.
                </li>
                <li>Never give your password to anyone.</li>
                <li>
                    Don&apos;t make any animations that you think would be offensive to others.
                    No excessive violence, no pornography.
                </li>
            </ul>
            <p>
                If you don&apos;t follow these rules you may be banned from Animasher.
                That means we lock your account so you can never use it again, and we may even delete all your animations.
                You won&apos;t be allowed to create a new account either.
                If we find out it&apos;s yours we will lock that too.
            </p>

            <h3 className="pinkheader">Recommendations</h3>
            <p style={{ margin: 0 }}>You don&apos;t <em>have</em> to follow these, but Animasher becomes a better place if you do:</p>
            <ul className="list">
                <li>If you see an animation that you don&apos;t like, just ignore it. Don&apos;t post a message saying it&apos;s bad.</li>
                <li>
                    If someone posts messages to you that you find disturbing, don&apos;t post back.
                    Instead, send a message to <Link to="/member/admin">admin</Link> and he will take care of it.
                </li>
                <li>
                    Welcome new users to Animasher.
                    If you see someone on Animasher that you haven&apos;t seen before, send them a message and just say hi.
                    Maybe ask them if they have any questions about how Animasher works.
                    New users don&apos;t know as much as you do.
                </li>
            </ul>
            <p>That&apos;s all. Have fun!</p>

            <p><Link to="/member/admin">admin</Link></p>
        </div>
    );
}
