import React from "react";
import Konva from "konva";
import {
    Stage as KonvaStage,
    Layer as KonvaLayer,
    Rect as KonvaRect,
    Shape as KonvaShape
} from "react-konva";
import PropTypes from "prop-types";

import Picture from "./media/Picture";

export default function Stage({ currentTime, animation, addMedia }) {
    const importEvents = {
        onDragOver(event) {
            event.preventDefault();
        },

        onDrop(event) {
            const rect = document.querySelector("canvas").getBoundingClientRect();
            const media = JSON.parse(event.dataTransfer.getData("media"));

            addMedia({
                ...media,

                // Position x, y
                p: [
                    {
                        t: 0,
                        v: [event.clientX - rect.x, event.clientY - rect.y]
                    }
                ],

                // Rotation
                r: [
                    {
                        t: 0,
                        v: 0
                    }
                ],

                // Size
                s: [
                    {
                        t: 0,
                        v: 0
                    }
                ],

                // Blur
                b: [
                    {
                        t: 0,
                        v: 0
                    }
                ],

                // Opacity
                o: [
                    {
                        t: 0,
                        v: 1
                    }
                ],

                // Filp vertical, horizontal
                f: [
                    {
                        t: 0,
                        v: [0, 0]
                    }
                ]
            });
        }
    };

    const editEvents = {
        onMouseDown(event) {
            event.target.to({
                duration: 0.1,
                easing: Konva.Easings.EaseOut,
                shadowColor: "red",
                shadowBlur: 3,
                shadowOpacity: 0.6
            });
        },

        onMouseUp(event) {
            event.target.to({
                duration: 0.1,
                easing: Konva.Easings.EaseOut,
                shadowBlur: 0,
                shadowOpacity: 0
            });
        },

        onDragEnd(event) {

        }
    };

    return (
        <div id="stage">
            <div id="tab">Animation area</div>
            <div className="content" onDrop={importEvents.onDrop} onDragOver={importEvents.onDragOver}>
                <KonvaStage width={720} height={560} >
                    {!animation.layers ? null : animation.layers.map((layer) => (
                        <KonvaLayer key={layer.id}>
                            <Picture
                                url={layer.url}
                                x={layer.p[0].v[0]}
                                y={layer.p[0].v[1]}
                                onMouseDown={editEvents.onMouseDown}
                                onMouseUp={editEvents.onMouseUp}
                                onDragEnd={editEvents.onDragEnd}
                            />
                        </KonvaLayer>
                    ))}
                    <KonvaLayer>
                        {/* Off-screen grayout */}
                        <KonvaShape
                            x={0}
                            y={0}
                            fill="#000"
                            opacity={0.1}
                            listening={false}
                            sceneFunc={(ctx, shape) => {
                                ctx.beginPath();
                                ctx.moveTo(0, 40);
                                ctx.lineTo(0, 0);
                                ctx.lineTo(720, 0);
                                ctx.lineTo(720, 560);
                                ctx.lineTo(0, 560);
                                ctx.lineTo(0, 40);
                                ctx.lineTo(40, 40);
                                ctx.lineTo(40, 520);
                                ctx.lineTo(680, 520);
                                ctx.lineTo(680, 40);
                                ctx.closePath();
                                ctx.fillStrokeShape(shape);
                            }}
                        ></KonvaShape>
                        <KonvaRect
                            x={40}
                            y={40}
                            width={640}
                            height={480}
                            stroke="#000"
                            opacity={0.1}
                            listening={false}
                        ></KonvaRect>
                    </KonvaLayer>
                </KonvaStage>
            </div>
        </div>
    );
};

Stage.propTypes = {
    currentTime: PropTypes.number.isRequired,
    animation: PropTypes.object.isRequired,
    addMedia: PropTypes.func.isRequired
};
