export function frameToFraction(current, count) {
    return current / count;
}

export function offsetToFraction(offset) {
    return offset / (640 - 80);
}

export function frameToMilliseconds(current) {
    return current * (1000 / 60);
}

export function millisecondsToFrame(current) {
    return current / (1000 / 60);
}
