import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faStop,
    faPlay,
    faCircle,
    faFastBackward,
    faStepBackward,
    faStepForward,
    faFastForward,
    faExternalLinkAlt,
    faVolumeUp
} from "@fortawesome/free-solid-svg-icons";
import PropTypes from "prop-types";

export default function TimelineControls({ isPlaying, startPlaying, stopPlaying, isRecording, startRecording, stopRecording }) {
    return (
        <div id="timeline-controls">
            <div className="group">
                <button
                    className="play"
                    onClick={isPlaying ? stopPlaying : startPlaying}
                >
                    <FontAwesomeIcon icon={isPlaying ? faStop : faPlay} />
                </button>
                <button className="record"><FontAwesomeIcon icon={faCircle} /></button>
            </div>

            <button className="auto-record enabled">Auto REC <FontAwesomeIcon icon={faCircle} size="sm" /> ON</button>

            <div className="group">
                <button className="skip-to-start"><FontAwesomeIcon icon={faFastBackward} /></button>
                <button className="step-backward"><FontAwesomeIcon icon={faStepBackward} /></button>
                <button className="step-forward"><FontAwesomeIcon icon={faStepForward} /></button>
                <button className="skip-to-end"><FontAwesomeIcon icon={faFastForward} /></button>
            </div>

            <button className="share"><FontAwesomeIcon icon={faExternalLinkAlt} /></button>
            <button className="save">Save</button>

            <span className="volume">
                <FontAwesomeIcon icon={faVolumeUp} />
                <input type="range" min="1" max="100" onChange={ (event) => console.log(event.target.value) } />
            </span>
        </div>
    );
}

TimelineControls.propTypes = {
    isPlaying: PropTypes.bool.isRequired,
    startPlaying: PropTypes.func.isRequired,
    stopPlaying: PropTypes.func.isRequired,
    isRecording: PropTypes.bool.isRequired,
    startRecording: PropTypes.func.isRequired,
    stopRecording: PropTypes.func.isRequired
};
