import React, { Component } from "react";
import PropTypes from "prop-types";

import clamp from "../../utils/clamp";
import { offsetToFraction, frameToFraction } from "../../utils/units";

export default class TimelineLayer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            ...props,

            frame: {
                ...props.frame
            },

            start: {
                isSeeking: false,
                seekInitialX: 0,
                seekInitialPosition: 0,
                seekPosition: frameToFraction(props.frame.start, props.frame.count) * 560
            },

            end: {
                isSeeking: false,
                seekInitialX: 0,
                seekInitialPosition: 0,
                seekPosition: frameToFraction(props.frame.end, props.frame.count) * 560
            }
        };
    }

    componentDidMount() {
        document.addEventListener("mouseup", this.onSeekStop.bind(this));
    }

    componentWillUnmount() {
        document.removeEventListener("mouseup", this.onSeekStop);
    }

    onSeekStart(handle, event) {
        const { clientX } = event;

        console.log("onSeekStart");

        this.setState((state) => ({
            ...state,

            [handle]: {
                ...state[handle],
                isSeeking: true,
                seekInitialX: clientX,
                seekInitialPosition: state[handle].seekPosition
            }
        }));
    }

    onSeekStop() {
        console.log("onSeekStop");
        this.setState((state) => ({
            ...state,

            start: {
                ...state.start,
                isSeeking: false
            },

            end: {
                ...state.end,
                isSeeking: false
            }
        }));
    }

    onMouseMove(event) {
        if (this.state.start.isSeeking) {
            this.updateHandle("start", event.clientX);
        }

        if (this.state.end.isSeeking) {
            this.updateHandle("end", event.clientX);
        }
    }

    updateHandle(handle, clientX) {
        const { seekInitialX, seekInitialPosition } = this.state[handle];

        const maxPixels = 640 - 80; // timelineWidth - seekerWidth

        const min = ({
            start: () => 0,
            end: () => offsetToFraction(this.state.start.seekPosition) * maxPixels
        })[handle]();

        const max = ({
            start: () => offsetToFraction(this.state.end.seekPosition) * maxPixels,
            end: () => maxPixels
        })[handle]();

        const seekPosition = clamp((seekInitialPosition + clientX - seekInitialX), min, max);

        this.setState((state) => ({
            ...state,

            [handle]: {
                ...state[handle],
                seekPosition
            },

            frame: {
                ...state.frame,
                [handle]: Math.floor(offsetToFraction(seekPosition) * state.frame.count)
            }
        }));
    }

    render() {
        return (
            <div className="layer" onMouseMove={this.onMouseMove.bind(this)}>
                <img src={this.state.url} alt={this.state.name} />
                <div className="track">
                    <div className="span"></div>
                    <div
                        className="handle start"
                        onMouseDown={this.onSeekStart.bind(this, "start")}
                        style={{ transform: `translate(${this.state.start.seekPosition}px)` }}
                    ></div>

                    <div
                        className="range"
                        onMouseDown={function(e) {
                            this.onSeekStart.bind(this, "start")(e);
                            this.onSeekStart.bind(this, "end")(e);
                        }.bind(this)}
                        style={{ left: this.state.start.seekPosition, right: 560 - this.state.end.seekPosition }}
                    ></div>

                    <div
                        className="handle end"
                        onMouseDown={this.onSeekStart.bind(this, "end")}
                        style={{ transform: `translate(${this.state.end.seekPosition - 560}px)` }}
                    ></div>
                </div>
            </div>
        );
    }
}

TimelineLayer.defaultProps = {
    url: null,
    name: null,
    frame: {
        start: 0,
        end: 0,
        count: 0
    }
};

TimelineLayer.propTypes = {
    url: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    frame: PropTypes.object.isRequired
};
