import React, { useState, useEffect } from "react";
import { nanoid } from "nanoid";

import Stage from "./Stage";
import Timeline from "./Timeline";
import TimelineControls from "./TimelineControls";

export default function Editor(props) {
    const [currentTime, setCurrentTime] = useState(0);
    const [isPlaying, setIsPlaying] = useState(false);
    const [isRecording, setIsRecording] = useState(true);

    const [animation, setAnimation] = useState({
        id: null,
        name: "",
        description: "",
        author: null,

        layers: []
    });

    // const seek = (time) => setCurrentTime(() => time);
    const startPlaying = () => setIsPlaying(() => true);
    const stopPlaying = () => setIsPlaying(() => false);
    const startRecording = () => setIsRecording(() => true);
    const stopRecording = () => setIsRecording(() => false);

    useEffect(() => {
        let isDestroyed = false;
        let time = +new Date();

        (function step() {
            const now = +new Date();
            const delta = now - time;

            if (isPlaying) {
                setCurrentTime((prevTime) => {
                    return prevTime + delta;
                });
            }

            time = now;

            if (!isDestroyed) window.requestAnimationFrame(step);
        })();

        return function cleanup() {
            isDestroyed = true;
        };
    }, [isPlaying]);

    const addMedia = (media) => {
        const { id, name, url, type, p, r, s, b, o, f } = media;

        console.log(media);
        setAnimation((state) => ({
            ...state,
            layers: [
                ...state.layers,
                {
                    id: nanoid(),
                    mediaId: id,
                    type,
                    name,
                    url,
                    p,
                    r,
                    s,
                    b,
                    o,
                    f
                }
            ]
        }));
    };

    return (
        <div id="editor">
            <Stage currentTime={currentTime} animation={animation} addMedia={addMedia}/>
            <TimelineControls
                isPlaying={isPlaying}
                startPlaying={startPlaying}
                stopPlaying={stopPlaying}

                isRecording={isRecording}
                startRecording={startRecording}
                stopRecording={stopRecording}
            />
            <Timeline currentTime={currentTime} animation={animation} />
        </div>
    );
}
