import React, { Component } from "react";
import Card from "../Card";
import { host } from "../../consts";

export default class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            member: null,
            animations: null
        };
    }

    async componentDidMount() {
        var res = await fetch(host + "/user/" + this.props.match.params.name);
        var user = await res.json();
        if (user.error) return console.error(user.error);

        this.setState({
            ...this.state,
            member: user
        });

        res = await fetch(host + "/user/" + this.props.match.params.name + "/animations");
        var anims = await res.json();
        if (anims.error) return console.error(anims.error);
        this.setState({
            ...this.state,
            animations: anims
        });
    }

    render() {
        const { name } = this.props.match.params;
        if (this.props.user && !this.state.member && name === this.props.user.name) {
            this.state = Object.assign(this.state, {
                member: this.props.user
            });
        }

        if (this.state.member === null) {
            return (
                <div className="container">Loading user...</div>
            );
        }
        const { member } = this.state;

        const canMessage = member.type >= 0 && member.id !== (this.props.user ? this.props.user.id : null);
        const isFollowing = canMessage ? true : null;

        return (
            <div className="container">
                <div className="grid">

                    <Card
                        title={member.name + " Has Created"}
                        className="animations"
                        content={
                            <>
                                No animations created yet
                            </>
                        }
                    />
                    <div className="profile">
                        <div className="title">
                            Profile
                        </div>
                        <div className="box">
                            <b>Hi, I&apos;m {member.name}</b>
                            {member.description}
                        </div>
                    </div>

                    {canMessage
                        ? <Card
                            title="Messages"
                            className="animations"
                            content={
                                <>
                                    <b>Send a message to {member.name}</b>
                                    <textarea></textarea>
                                    <button>Post message</button>
                                </>
                            }
                        /> : ""}
                    {isFollowing === null ? ""
                        : <div className={"follow " + isFollowing ? "active" : "notactive"}>
                            {isFollowing ? "STOP FOLLOWING THIS MEMBER" : "FOLLOW"}
                        </div>}
                </div>
            </div>
        );
    }
}
