import React, { useState, createRef } from "react";
import { Link } from "react-router-dom";
import { host } from "../../../consts";

export default function Login(props) {
    const [disabled, setDisabled] = useState(false);
    const [loading, setLoading] = useState(false);

    const usernameField = createRef();
    const passwordField = createRef();

    const loginInput = () => {
        if (usernameField.current.value.length > 0 && passwordField.current.value.length > 0) setDisabled(false);
        else setDisabled(true);
    };

    const login = async e => {
        e.preventDefault();

        var fields = {
            name: usernameField.current.value,
            password: passwordField.current.value
        };
        setLoading(true);

        var resp = await fetch(host + "/user/login", {
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(fields)
        });
        var res = await resp.json();

        if (res.error || !res.token) {
            setLoading(false);
            switch (res.error) {
                case "login_error":
                    return console.error("Name or password is incorrect");

                default:
                    return console.error("Unknown error");
            }
        }

        localStorage.setItem("token", res.token);
        props.updateUser(res.user);
    };

    return (
        <div className="login">
            <Link to="/signup">signup</Link><span>/</span>
            <form
                action=""
                id="login"
                method="GET"
                onSubmit={login}
            >
                <input
                    type="text"
                    name="username"
                    id="username"
                    placeholder="login"
                    onInput={loginInput}
                    ref={usernameField}
                />
                <input
                    type="password"
                    name="password"
                    id="password"
                    placeholder="••••••••"
                    onInput={loginInput}
                    ref={passwordField}
                />
                <input
                    type="submit"
                    value="ok"
                    disabled={(disabled || loading)}
                />
            </form>
        </div>
    );
}
