import React, { useEffect, useRef } from "react";

export default function About() {
    const glitch = useRef();
    useEffect(() => {
        glitch.current.setAttribute("data-glitch-text", glitch.current.innerText);
        glitch.current.onclick = () => {
            glitch.current.hasAttribute("disabled") ? glitch.current.removeAttribute("disabled") : glitch.current.setAttribute("disabled", "true");
        };
    });
    return (
        <div id="about">
            <h1 className="pink">What was animasher?</h1>
            <p>
                Animasher was an animation website that shut down in 2012.
                It has left such a big impact on the community that Animasher was still being talked about a decade after being shut down.
                It has inspired many artists and creators in the early 2000&apos;s.
                The site was for all ages but the community of Animasher was mostly children aged 12-16.
                The original founder of Animasher wrote:
                <br />
                <em>
                    We sincerely believe that using Animasher is one of the best things children can do online today.
                    Unlike merely watching movies, Animasher is a very active form of entertainment.
                    And unlike playing games, Animasher is a creative activity.
                    Kids use Animasher to tell stories, talk about their interests, create tutorials, and much else.
                    It is challenging, unlimited, and empowering in the same way that a pen and a blank piece of paper is.
                    Many teachers also use Animasher in the classroom.
                    One of them emailed us personally to say that participation and interest in her classes had been at an all time high ever since she introduced Animasher.
                </em>
            </p>

            <h1 className="pink">What is Animasher.net?</h1>
            <p>
                Animasher.net is a recreation of Animasher with very minimal tweaks. It will look and function exactly as Animasher did.
                We&apos;re a <em>very</em> small team from across Europe. This is our first website.
                Please be nice. <em className="glitch" ref={glitch} disabled>we&apos;re being held against our will, please hel-</em>
            </p>
        </div>
    );
}
