import React from "react";
import { Link } from "react-router-dom";

import Card from "../Card";
import RecentPosts from "../RecentPosts";
import RecentActivities from "../RecentActivities";

import video from "../../assets/img/fpvid.mp4";

export default function Home() {
    return (
        <div className="container" id="home">
            <Card
                title="Recently Posted"
                className="recent"
                content={<RecentPosts />}
                footer={{ text: "see more", link: "/watch" }}
            />
            <div className="middle">
                <div className="intro white-box">
                    <h1>This is a place where you make animations</h1>
                    <p>Make animations! No downloads, all free.</p>
                    <Link to="/create">CLICK HERE TO MAKE ONE NOW!</Link>
                </div>
                <div className="video white-box">
                    <video
                        src={video}
                        controls
                        style={{ maxWidth: "100%" }}
                    >
                        Your browser doesn&apos;t support the <code>video</code> tag.
                    </video>
                </div>
                <Card title="Make Animations" className="steps" >
                    <p>OK, so this stuff is really pretty simple:</p>
                    <p>1. Choose a picture (a cat, a sleeping princess, a computer - whatever you want!) and drag it around</p>
                    <p>2. Add some sound effects</p>
                    <p>3. Save it and show it to your friends</p>
                    <p>4. No fourth step :)</p>
                    <p className="doit"><Link to="/create">DO IT!<i></i></Link></p>
                </Card>
            </div>
            <div>
                <Card
                    title="Recent Activity"
                    content={<RecentActivities />}
                />

                <Card title="Readme (English)" className="readme">
                    <p>Please have a seat, take a deep breath, and then log in to read my message. It&apos;s important.</p>
                    <Link to="/member/admin">Admin</Link>
                </Card>

            </div>
        </div>
    );
}
