import React, { useState } from "react";
import { nanoid } from "nanoid";

export default function Library(props) {
    const [activeTab, setActiveTab] = useState("picture");

    const [mediaList] = useState([
        { id: nanoid(), type: "picture", name: "dog", url: "test-memes/dog.png" },
        { id: nanoid(), type: "picture", name: "doggo", url: "test-memes/doggo.jpg" },
        { id: nanoid(), type: "picture", name: "fish", url: "test-memes/fish.png" },
        { id: nanoid(), type: "picture", name: "grumpy-no", url: "test-memes/grumpy-no.jpg" },
        { id: nanoid(), type: "picture", name: "kitten", url: "test-memes/kitten.jpg" },
        { id: nanoid(), type: "picture", name: "pipe-dream", url: "test-memes/pipe-dream.jpg" }
    ]);

    const onDragStart = (event) => {
        const id = event.target.dataset.id;
        const media = mediaList.find((item) => item.id === id);

        console.log("dragStart", media);
        event.dataTransfer.setData("media", JSON.stringify(media));
    };

    const tabs = [
        {
            mediaType: "picture",
            title: "OBJECTS",
            category: "static"
        },
        {
            mediaType: "background",
            title: "BACKGROUND",
            category: "static"
        },
        {
            mediaType: "text",
            title: "TEXT",
            category: "static"
        },
        {
            mediaType: "music",
            title: "MUSIC",
            category: "playable"
        },
        {
            mediaType: "effects",
            title: "EFFECTS",
            category: "playable"
        },
        {
            mediaType: "mic",
            title: "MIC",
            category: "playable"
        },
        {
            mediaType: "video",
            title: "VIDEO",
            category: "playable"
        }
    ];

    return (
        <div id="library">
            <div className="tab active">Library</div>
            <div className="content">
                {mediaList.filter((item) => item.type === activeTab).map((item, index) => {
                    return <img key={item.name} src={item.url} alt={item.name} data-id={item.id} draggable="true" onDragStart={onDragStart} />;
                })}
            </div>
            <div className="tabs">
                {tabs.map((tab) => (
                    <div
                        key={tab.mediaType}
                        className={`tab ${tab.category} ${activeTab === tab.mediaType ? "active" : ""}`}
                        onClick={() => setActiveTab(tab.mediaType)}
                    >{tab.title}</div>
                ))}
            </div>
        </div>
    );
}
