import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

function Card(props) {
    let footer = "";
    if (props.footer.text && props.footer.link) footer = <h3 className="card--footer"><Link to={props.footer.link}>{props.footer.text}</Link></h3>;

    return (
        <div className={"card " + props.className}>
            <p className="card--title">{props.title}</p>
            <div className={"card--content box"}>{props.content || props.children}</div>
            {footer}
        </div>
    );
}

Card.propTypes = {
    title: PropTypes.string.isRequired,
    footer: PropTypes.object,
    className: PropTypes.string,
    content: (props, propName, componentName) => {
        if (!props.content && !props.children) {
            return new Error(`One of props "${propName}" or "children" was not specified in "${componentName}".`);
        }
    },
    children: (props, propName, componentName) => {
        if (!props.content && !props.children) {
            return new Error(`One of props "content" or "${propName}" was not specified in "${componentName}".`);
        }
    }
};

Card.defaultProps = {
    footer: {},
    className: ""
};

export default Card;
