// MODULES
import React, { Component } from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

// PAGES
import Page404 from "./components/pages/Page404";
import Home from "./components/pages/Home";
import Feedback from "./components/pages/Feedback";
import Terms from "./components/pages/Terms";
import License from "./components/pages/License";
import About from "./components/pages/About";
import Rules from "./components/pages/Rules";
import Parents from "./components/pages/Parents";
import Contact from "./components/pages/Contact";
import Register from "./components/pages/Register";
import Create from "./components/pages/Create";

// LAYOUT
import Header from "./components/layout/Header";
import Footer from "./components/layout/Footer";

// SASS
import "./index.scss";
import Profile from "./components/pages/Profile";
import Watch from "./components/pages/Watch";
import { host } from "./consts";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: null
        };

        this.updateUser = async (user, token) => {
            if (!user) {
                token = localStorage.getItem("token");
                if (!token) {
                    this.setState({
                        user: false
                    });
                    return;
                }
                var res = await fetch(host + "/user", {
                    headers: {
                        Authorization: "token " + token
                    }
                });
                user = await res.json();
                if (user.error) {
                    localStorage.removeItem("token");
                    this.setState({
                        user: false
                    });
                    return;
                }
            }

            user.makeRequest = async (url, settings) => {
                var res = await fetch(url, {
                    ...settings,
                    headers: {
                        ...settings.headers,
                        "Content-type": "application/json",
                        Authorization: token
                    }
                });
                return await res.json();
            };

            this.setState({
                user
            });
        };
    }

    componentDidMount() {
        this.updateUser();
    }

    render() {
        if (this.state.user === null) {
            return (
                <p>Loading</p>
            );
        }
        console.log(this.state.user);
        return (
            <Router>
                <Header user={this.state.user} updateUser={this.updateUser}/>
                <main>
                    <Switch>
                        <Route exact path="/" render={route => (
                            <Home {...route} user={this.state.user} />
                        )}/>
                        <Route exact path="/feedback" component={Feedback}/>
                        <Route exact path="/tos" component={Terms}/>
                        <Route exact path="/license" component={License}/>
                        <Route exact path="/about" component={About}/>
                        <Route exact path="/rules" component={Rules}/>
                        <Route exact path="/parents" component={Parents}/>
                        <Route exact path="/contact" component={Contact}/>
                        { this.state.user ? ""
                            : <Route exact path="/signup" component={Register}/> }

                        <Route path="/member/:name" render={route => (
                            <Profile {...route} user={this.state.user} />
                        )}/>
                        <Route path="/watch/:id" component={Watch}/>

                        <Route exact path="/create" component={Create}/>

                        <Route component={Page404} />
                    </Switch>
                </main>
                <Footer />
            </Router>
        );
    }
}

ReactDOM.render(
    <App />,
    document.getElementById("root")
);
